import React from "react";
import { View, Dimensions } from "react-native";

const width = Dimensions.get("window").width;
export default ({
  size = 12,
  children,
  sizexs,
  sizesm,
  sizemd,
  sizelg,
  sizexl,
  sizexxl
}: {
  size?: number;
  children?: any;
  sizexs?: number;
  sizesm?: number;
  sizemd?: number;
  sizelg?: number;
  sizexl?: number;
  sizexxl?: number;
}) => {
  let current: any = size;

  if (width <= 410) {
    current = sizesm || size;
  } else if (width >= 411 && width <= 567) {
    current = sizesm || size;
  } else if (width >= 568 && width <= 767) {
    current = sizemd || size;
  } else if (width >= 768 && width <= 1023) {
    current = sizelg || size;
  } else if (width >= 1024 && width <= 1279) {
    current = sizexl || size;
  } else if (width >= 1280) {
    current = sizexxl || size;
  }

  console.log(width, current, sizesm, sizexs);

  return (
    <View
      style={{
        flexBasis: (current / 12) * 100 + "%"
      }}
    >
      {children}
    </View>
  );
};
